

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
}

#game {
  display: flex;
  flex-direction: column;
}

header {
  height: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
}

header span {
  flex: 1;
}

#title {
  color: white;
  font-weight: bold;
  vertical-align: middle;
}

#menu {
  color: gray;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 25px;
}

#guessRow {
  display: flex;
  flex-direction: row;
}

.guessChar {

  width: 60px;
  height: 60px;
  line-height: 60px;
  vertical-align: top;
  margin: 3px;

  background-color: #3a3a3c;
  text-align: center;
  text-transform: capitalize;
  color: white;
  font-size: 30px;
  font-weight: bold;
}

.guessChar.eat {
  background-color: #538d4e;
}
.guessChar.bite {
  background-color: #b59f3b;
}

#form {
  margin: 3px
}

#input {
  width: 305px;
  height: 60px;
  padding-left: 17px;
  letter-spacing: 10px;
  background-color: #3a3a3c;

  border-width: 0px;
  font-family: "Helvetica Neue";
  font-size: 25px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;

  overflow-wrap: normal;
}